import React, { Component, PropTypes } from 'react'
import Footer from '../components/Footer'
import Header from '../components/Header'
import Hero from '../components/Hero'
import Link from 'gatsby-link'
import Preview from '../components/Preview'
import WheelReact from 'wheel-react'
import data from '../data/preview'
import Img from 'gatsby-image'
import layout from '../style/layout'
import text from '../style/type'
import { css } from 'glamor'
import { navigateTo } from 'gatsby-link'

class IndexPage extends Component {
  constructor(props) {
    super(props)
    this.state = {
      animating: true,
      content: '',
      leaving: false,
      loaded: false,
      pageIndex: 0,
      ready: false,
    }
    this.handleOnClick = this.handleOnClick.bind(this)
    this.handleOnClickLeave = this.handleOnClickLeave.bind(this)
    WheelReact.config({
      up: () => {
        this.handleOnClick('up')
      },
      down: () => {
        this.handleOnClick('down')
      },
    })
  }

  componentDidMount() {
    this.setState({ loaded: true })
    setTimeout(() => {
      this.setState({ ready: true, loaded: false })
    }, 1400)
  }

  componentWillUnmount() {
    WheelReact.clearTimeout()
  }

  handleOnClickLeave(loc) {
    this.setState({ leaving: true })
    setTimeout(() => {
      navigateTo(loc)
    }, 900)
  }

  handleOnClick(direction) {
    const total = data.length
    const { animating, pageIndex, ready } = this.state

    if (ready === true) {
      this.setState({ animating: true, ready: false })

      if (direction === 'up') {
        setTimeout(() => {
          total === pageIndex + 1
            ? this.setState({ animating: false, pageIndex: 0 })
            : this.setState({ animating: false, pageIndex: pageIndex + 1 })
        }, 900)
      } else {
        setTimeout(() => {
          pageIndex === 0
            ? this.setState({ animating: false, pageIndex: total - 1 })
            : this.setState({ animating: false, pageIndex: pageIndex - 1 })
        }, 900)
      }

      setTimeout(() => {
        this.setState({ ready: true })
      }, 1800)
    }
  }

  render() {
    const { ht100, leftCol, rightCol } = layout
    const { link } = text

    return (
      <div {...ht100} {...WheelReact.events}>
        <section {...leftCol}>
          <Hero
            animating={this.state.animating}
            data={data[this.state.pageIndex]}
            handleOnClickLeave={this.handleOnClickLeave}
            leaving={this.state.leaving}
            loaded={this.state.loaded}
            ready={this.state.ready}
          />
        </section>
        <main {...rightCol}>
          <Header />
          <Preview
            animating={this.state.animating}
            data={data[this.state.pageIndex]}
            leaving={this.state.leaving}
            loaded={this.state.loaded}
            ready={this.state.ready}
          />
          <Footer handleOnClick={() => this.handleOnClick('up')} />
        </main>
      </div>
    )
  }
}

export default IndexPage



