import React, { PropTypes } from 'react'
import Link from 'gatsby-link'
import animation from '../style/animation'
import background from '../style/background'
import classNames from 'classnames'
import { css } from 'glamor'

const Hero = ({
  animating,
  data = {},
  handleOnClickLeave,
  leaving,
  loaded,
  ready,
}) => (
  <div
    className={animating && `${animation.page}`}
    onClick={() => handleOnClickLeave(data.slug)}
  >
    <div
      className={classNames({
        [animation.pageSwipe]: true,
        [animation.swipeIn]: !ready,
        [animation.swipeIntro]: loaded,
        [animation.swipeLeave]: leaving,
      })}
    />
    <div {...background.default} {...data.img} />
    <span {...background.cta}>VIEW PROJECT</span>
  </div>
)

Hero.PropTypes = {
  animating: PropTypes.bool,
  data: PropTypes.object,
  handleOnClickLeave: PropTypes.func,
  leaving: PropTypes.bool,
  loaded: PropTypes.bool,
  ready: PropTypes.bool,
}

export default Hero



